import { InjectionToken, Provider } from '@angular/core';
import { Countries as DefaultCountriesList, defaultAccountTypes } from './constants';
import { IBANCountryList } from './directives/iban-validator/countrySpec';

const emptyIbanCountriesList: IBANCountryList = {};

export const BB_CONTACT_COUNTRIES_LIST: InjectionToken<string> = new InjectionToken('BB_CONTACT_COUNTRIES_LIST');

export const BB_CONTACT_IBAN_COUNTRIES_LIST: InjectionToken<string> = new InjectionToken(
  'BB_CONTACT_IBAN_COUNTRIES_LIST',
);

export const BB_CONTACT_BANK_ACCOUNT_TYPES_LIST: InjectionToken<string> = new InjectionToken(
  'BB_CONTACT_BANK_ACCOUNT_TYPES_LIST',
);

export const ContactConfigProviders: Array<Provider> = [
  {
    provide: BB_CONTACT_COUNTRIES_LIST,
    useValue: DefaultCountriesList,
  },
  {
    provide: BB_CONTACT_IBAN_COUNTRIES_LIST,
    useValue: emptyIbanCountriesList,
  },
  {
    provide: BB_CONTACT_BANK_ACCOUNT_TYPES_LIST,
    useValue: defaultAccountTypes,
  },
];
